import React, { useState, useEffect } from 'react';
import axios from 'axios';
import moment from 'moment';
import { getWeatherIcon } from './utils/weatherIcons';

const API_KEY = 'f00c38e0279b7bc85480c3fe775d518c'; 
const CurrentLocationWeather = () => {
  const [weather, setWeather] = useState(null);
  const [error, setError] = useState('');

  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(position => {
        const { latitude, longitude } = position.coords;
        fetchWeather(latitude, longitude);
      }, () => {
        setError('Geolocation not supported or permission denied.');
      });
    } else {
      setError('Geolocation is not supported by this browser.');
    }
  }, []);

  const fetchWeather = async (lat, lon) => {
    try {
      const response = await axios.get(
        `https://api.openweathermap.org/data/2.5/weather`,
        {
          params: {
            lat,
            lon,
            units: 'metric',
            appid: API_KEY,
          },
        }
      );
      setWeather(response.data);
    } catch (err) {
      setError('Failed to fetch weather data.');
    }
  };

  if (error) return <div className="error">{error}</div>;
  if (!weather) return <div>Loading current location weather...</div>;

  const weatherMain = weather.weather[0].main;
  const isDay = weather.dt > weather.sys.sunrise && weather.dt < weather.sys.sunset;

  return (
    <div className="current-weather">
      <h2>Current Location Weather</h2>
      <p className="location-name">{weather.name}</p>
      <p className="current-date">{moment.unix(weather.dt).format('LLLL')}</p>
      <div className="weather-info">
        <div className="weather-icon">
          {getWeatherIcon(weatherMain, isDay)}
        </div>
        <div className="weather-details">
          <p className="temperature">Temperature: {weather.main.temp}°C</p>
          <p className="condition">Condition: {weather.weather[0].description}</p>
          <p className="humidity">Humidity: {weather.main.humidity}%</p>
          <p className="wind">Wind Speed: {weather.wind.speed} m/s</p>
        </div>
      </div>
    </div>
  );
};

export default CurrentLocationWeather;
