import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { getWeatherIcon } from './utils/weatherIcons';

const API_KEY = 'f00c38e0279b7bc85480c3fe775d518c'; 

const SurroundingCities = () => {
  const [highestTempCities, setHighestTempCities] = useState([]);
  const [lowestTempCities, setLowestTempCities] = useState([]);
  const [error, setError] = useState('');

  useEffect(() => {
    // Fetch surrounding cities using current location
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(position => {
        const { latitude, longitude } = position.coords;
        fetchSurroundingCities(latitude, longitude);
      }, () => {
        setError('Geolocation not supported or permission denied.');
      });
    } else {
      setError('Geolocation is not supported by this browser.');
    }
  }, []);

  const fetchSurroundingCities = async (lat, lon) => {
    try {
      // Using 'find' API to get cities within a circle (10km radius)
      const response = await axios.get(
        `https://api.openweathermap.org/data/2.5/find`,
        {
          params: {
            lat,
            lon,
            cnt: 50, // Number of cities to fetch
            units: 'metric',
            appid: API_KEY,
          },
        }
      );
      const fetchedCities = response.data.list;

      // Sort to get top 10 highest and lowest temperatures
      const sortedByTemp = [...fetchedCities].sort((a, b) => b.main.temp - a.main.temp);
      setHighestTempCities(sortedByTemp.slice(0, 10));

      const sortedByLowestTemp = [...fetchedCities].sort((a, b) => a.main.temp - b.main.temp);
      setLowestTempCities(sortedByLowestTemp.slice(0, 10));

    } catch (err) {
      setError('Failed to fetch surrounding cities weather data.');
    }
  };

  if (error) return <div className="error">{error}</div>;
  if (highestTempCities.length === 0 || lowestTempCities.length === 0) return <div>Loading surrounding cities weather...</div>;

  return (
    <div className="surrounding-cities">
      <h2>Top 10 Highest Temperatures Nearby</h2>
      <ul className="city-list">
        {highestTempCities.map(city => (
          <li key={city.id} className="city-item">
            <span className="city-name">{city.name}</span>
            <span className="city-temp">{city.main.temp}°C</span>
            <span className="city-icon">{getWeatherIcon(city.weather[0].main)}</span>
          </li>
        ))}
      </ul>

      <h2>Top 10 Lowest Temperatures Nearby</h2>
      <ul className="city-list">
        {lowestTempCities.map(city => (
          <li key={city.id} className="city-item">
            <span className="city-name">{city.name}</span>
            <span className="city-temp">{city.main.temp}°C</span>
            <span className="city-icon">{getWeatherIcon(city.weather[0].main)}</span>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default SurroundingCities;
