import React, { useState, useEffect } from 'react';
import axios from 'axios';
import moment from 'moment';
import { getWeatherIcon } from './utils/weatherIcons';

const API_KEY = 'f00c38e0279b7bc85480c3fe775d518c'; 
const WeatherReport = ({ city }) => {
  const [dailyForecast, setDailyForecast] = useState([]);
  const [hourlyForecast, setHourlyForecast] = useState([]);
  const [error, setError] = useState('');

  useEffect(() => {
    fetchWeatherReport(city);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [city]);

  const fetchWeatherReport = async (cityName) => {
    try {
      // Get coordinates for the city
      const geoResponse = await axios.get(
        `https://api.openweathermap.org/geo/1.0/direct`,
        {
          params: {
            q: cityName,
            limit: 1,
            appid: API_KEY,
          },
        }
      );

      if (geoResponse.data.length === 0) {
        setError('City not found.');
        return;
      }

      const { lat, lon } = geoResponse.data[0];

      // Fetch One Call API for detailed forecast
      const weatherResponse = await axios.get(
        `https://api.openweathermap.org/data/2.5/onecall`,
        {
          params: {
            lat,
            lon,
            exclude: 'minutely,alerts',
            units: 'metric',
            appid: API_KEY,
          },
        }
      );

      setDailyForecast(weatherResponse.data.daily);
      setHourlyForecast(weatherResponse.data.hourly);
    } catch (err) {
      setError('Failed to fetch weather report.');
    }
  };

  if (error) return <div className="error">{error}</div>;
  if (dailyForecast.length === 0 || hourlyForecast.length === 0) return <div>Loading weather report...</div>;

  return (
    <div className="weather-report">
      <h2>Weather Report for {city}</h2>

      <h3>Daily Forecast</h3>
      <ul className="daily-forecast">
        {dailyForecast.map(day => {
          const weatherMain = day.weather[0].main;
          const isDay = day.dt > day.sunrise && day.dt < day.sunset;
          return (
            <li key={day.dt} className="forecast-item">
              <span className="forecast-date">{moment.unix(day.dt).format('dddd, MMM D')}</span>
              <span className="forecast-icon">{getWeatherIcon(weatherMain, isDay)}</span>
              <span className="forecast-temp">{day.temp.day}°C</span>
              <span className="forecast-description">{day.weather[0].description}</span>
            </li>
          );
        })}
      </ul>

      <h3>Hourly Forecast</h3>
      <ul className="hourly-forecast">
        {hourlyForecast.slice(0, 24).map(hour => {
          const weatherMain = hour.weather[0].main;
          // Since 'sunrise' and 'sunset' aren't directly available in hourly data,
          // we can omit 'isDay' or pass a default value.
          return (
            <li key={hour.dt} className="forecast-item">
              <span className="forecast-time">{moment.unix(hour.dt).format('h A')}</span>
              <span className="forecast-icon">{getWeatherIcon(weatherMain)}</span>
              <span className="forecast-temp">{hour.temp}°C</span>
              <span className="forecast-description">{hour.weather[0].description}</span>
            </li>
          );
        })}
      </ul>
    </div>
  );
};

export default WeatherReport;
