import React from 'react';
import {
  WiDaySunny,
  WiCloud,
  WiRain,
  WiSnow,
  WiThunderstorm,
  WiFog,
  WiDayCloudy,
  WiNightClear,
  WiNightAltCloudy,
} from 'react-icons/wi';

export const getWeatherIcon = (weatherMain, isDay = true) => {
  switch (weatherMain) {
    case 'Clear':
      return isDay ? <WiDaySunny size={50} /> : <WiNightClear size={50} />;
    case 'Clouds':
      return isDay ? <WiDayCloudy size={50} /> : <WiNightAltCloudy size={50} />;
    case 'Rain':
    case 'Drizzle':
      return <WiRain size={50} />;
    case 'Snow':
      return <WiSnow size={50} />;
    case 'Thunderstorm':
      return <WiThunderstorm size={50} />;
    case 'Mist':
    case 'Smoke':
    case 'Haze':
    case 'Dust':
    case 'Fog':
    case 'Sand':
    case 'Ash':
    case 'Squall':
    case 'Tornado':
      return <WiFog size={50} />;
    default:
      return <WiDaySunny size={50} />; // Default icon
  }
};
